/* Estilos del Contenedor Principal */
.credential-container {
  width: 400px;
  border: none;
  box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.2);
  font-family: 'Arial', sans-serif;
  display: flex;
  flex-direction: column;
  margin: 20px auto;
  border-radius: 15px;
  overflow: hidden;
}

/* Estilos del Encabezado */
.header {
  background: linear-gradient(to bottom, #98fb98, #008000);
  padding: 20px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-bottom: 2px solid #006400;
}

.logo-container {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 15px;
  border: 3px solid #006400;
  overflow: hidden;
  background-color: #fff;
}

.logo {
  max-width: 90%;
  max-height: 90%;
  display: block;
}

.title {
  font-size: 1.5rem;
  color: #fff;
  margin-bottom: 5px;
  font-weight: bold;
}

.subtitle {
  font-size: 1.1rem;
  color: #fff;
  margin-bottom: 15px;
}

/* Estilos del Cuerpo */
.body {
  padding: 20px;
  background-color: #fff;
}

.body p {
  margin-bottom: 10px;
  font-size: 1rem;
  color: #000;
}

.body strong {
  color: #000;
  font-weight: bold;
}

.body ul {
  list-style: none;
  padding: 0;
  margin-bottom: 20px;
}

.body li {
  margin-bottom: 10px;
  font-size: 1rem;
  color: #000;
}

.body li::before {
  content: "â€¢";
  color: #008000;
  display: inline-block;
  width: 1em;
  margin-left: -1em;
}

/* Estilos del Pie */
.footer {
  background-color: #f5f5f5;
  padding: 15px;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  border-top: 2px solid #006400;
}

.footer p {
  font-size: 0.9rem;
  color: #000;
  margin: 0;
}

.line {
  height: 2px;
  background-color: #000;
  margin-top: 5px;
  margin-bottom: 10px;
}

.doctor-info {
  margin-bottom: 15px;
}

.doctor-info p {
  margin-bottom: 0px;
}

.qr-code {
  margin-top: 10px;
  margin-bottom: 20px;
}

/* Progreso y Sistema de Niveles */
.progress-section {
  margin-top: 20px;
  padding: 15px;
  background-color: #f8f9fa;
  border-radius: 8px;
  border: 1px solid #dee2e6;
}

.progress {
  margin-top: 20px;
  font-size: 1.1rem;
  font-weight: bold;
  color: #006400;
  margin-bottom: 15px;
}

.code-input-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 12px;
  margin: 15px 0;
}

.code-input {
  width: 80%;
  padding: 12px;
  border: 2px solid #4A90E2;
  border-radius: 5px;
  font-size: 1rem;
  text-align: center;
  transition: border-color 0.3s ease;
  outline: none;
}

.code-input:focus {
  border-color: #357ABD;
  box-shadow: 0 0 0 2px rgba(74, 144, 226, 0.2);
}

.error-text {
  color: #dc3545;
  font-size: 0.9rem;
  margin-top: 5px;
  text-align: center;
  font-weight: 500;
  padding: 5px 10px;
  background-color: #fff3f3;
  border-radius: 4px;
  width: 80%;
}

/* Botones */
.start-button, .progress-button {
  background-color: #4A90E2;
  color: white;
  padding: 12px 24px;
  font-size: 1rem;
  font-weight: 600;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s ease;
  width: 80%;
  margin: 10px 0;
}

.start-button:hover, .progress-button:hover {
  background-color: #357ABD;
  transform: translateY(-1px);
}

.start-button:active, .progress-button:active {
  transform: translateY(1px);
}

.progress-button:disabled {
  background-color: #cccccc;
  cursor: not-allowed;
  transform: none;
}

/* Estilos del Formulario */
.form-container {
  display: flex;
  flex-direction: column;
  width: 400px;
  margin: 20px auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 10px;
  box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.2);
  background-color: white;
}

.input-group {
  margin-bottom: 15px;
}

.input-group label {
  display: block;
  margin-bottom: 8px;
  font-weight: 500;
  color: #333;
}

.input-group input {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-sizing: border-box;
  transition: border-color 0.3s ease;
}

.input-group input:focus {
  border-color: #4A90E2;
  outline: none;
  box-shadow: 0 0 0 2px rgba(74, 144, 226, 0.2);
}

.submit-button {
  background-color: #008000;
  color: white;
  border: none;
  padding: 12px 24px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
  font-weight: 600;
  transition: all 0.3s ease;
  margin-top: 10px;
}

.submit-button:hover {
  background-color: #006400;
  transform: translateY(-1px);
}

.submit-button:active {
  transform: translateY(1px);
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  padding: 20px;
}

/* Mensaje de Completado */
.completion-message {
  color: #28a745;
  font-weight: bold;
  font-size: 1.1rem;
  margin: 15px 0;
  padding: 10px;
  background-color: #d4edda;
  border-radius: 5px;
  text-align: center;
  width: 80%;
}

/* Animaciones */
@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

.progress-section, .code-input-section, .completion-message {
  animation: fadeIn 0.3s ease-in;
}