/* AchievementModal.css */
.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
  }
  
  .modal-content {
    background: white;
    padding: 20px;
    border-radius: 15px;
    position: relative;
    width: 300px;
    max-width: 90%;
  }
  
  .modal-close {
    position: absolute;
    top: -10px;
    right: -10px;
    background: #ff4444;
    color: white;
    border: none;
    border-radius: 50%;
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    font-size: 18px;
    z-index: 1001;
  }
  
  .flip-card {
    background-color: transparent;
    width: 100%;
    height: 400px;
    perspective: 1000px;
    cursor: pointer;
  }
  
  .flip-card-inner {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    transition: transform 0.8s;
    transform-style: preserve-3d;
  }
  
  .flip-card.flipped .flip-card-inner {
    transform: rotateY(180deg);
  }
  
  .flip-card-front,
  .flip-card-back {
    position: absolute;
    width: 100%;
    height: 100%;
    backface-visibility: hidden;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: white;
    border-radius: 10px;
    padding: 20px;
  }
  
  .flip-card-back {
    transform: rotateY(180deg);
    background: #f8f9fa;
  }
  
  .badge-icon {
    font-size: 5rem;
    margin-bottom: 20px;
  }
  
  .badge-title {
    font-size: 2rem;
    margin-bottom: 10px;
    color: #2c3e50;
  }
  
  .badge-message {
    font-size: 1.2rem;
    color: #34495e;
    margin-bottom: 20px;
  }
  
  .meme-emoji {
    font-size: 3rem;
    margin-bottom: 20px;
  }
  
  .meme-text {
    font-size: 1.2rem;
    color: #2c3e50;
    margin-bottom: 20px;
  }
  
  .flip-hint {
    font-size: 0.9rem;
    color: #666;
    margin-top: 20px;
  }
  
  @keyframes modalPop {
    from {
      transform: scale(0.8);
      opacity: 0;
    }
    to {
      transform: scale(1);
      opacity: 1;
    }
  }
  
  .modal-content {
    animation: modalPop 0.3s ease-out;
  }